<template>
  <div id="app">
    <h1 style="text-align: center;">打工人日历</h1>
    <el-row class="range">
      <span>范围: </span>
      <el-date-picker
        style="margin-left:20px"
        v-model="daterange"
        type="daterange"
        range-separator="至"
        start-placeholder="开始日期"
        end-placeholder="结束日期"
        @change="dateChange">
      </el-date-picker>
      <span style="margin-left:20px"><el-button @click="init">重置</el-button></span>
    </el-row>
    <el-row class="count">
      共{{ dateList.count }}天,&#129521;{{ dateList.workCount }}天,&#127796;{{ dateList.count-dateList.workCount }}天。
    </el-row>
    <el-row class="tips">
      {{ today }}<br/>{{ tips }}
    </el-row>
    <el-row>
      <el-col :span='27' :key="index"  v-for='(item,index) in ["一","二","三","四","五","六","日"]'>
        {{ "星期"+item }}
      </el-col>
    </el-row>
    <el-row>
      <el-col :span='27' :key="index"  v-for='(item,index) in dateList'>
      <el-card v-if="item.date" @click.native="changeState(index,item)">
        {{ item.date }}
        <span>{{ item.holiday?'&#127796;':'&#129521;' }}</span>
      </el-card>
    </el-col>
    </el-row>
  </div>
</template>

<script>


export default {
  name: 'App',
  created() {
    this.init()
  },
  data() {
    return {
      daterange: [],
      stime: null,
      etime: null,
      dateList: [],
      holiday: null,
      today: '',
      todayInfo: '',
      tips: '',
      workTips: [
        '我要悄悄打工，然后惊艳所有人!',
        '不干活，就没饭吃。加油，打工人！',
        '没有困难的工作，只有勇敢的打工人！',
        '爱情不是生活的全部，打工才是。加油，打工人！',
        '累吗？累就对了，舒服是留给有钱人的。加油，打工人！',
        '冷吗？冷就对了，温暖是留给开小轿车的人。加油，共享单车人 ！',
        '奶茶好喝吗？好喝就对了！奶茶是奖励给打工人心灵慰籍的一剂良药！加油，打工人！',
        '有人相爱，有人夜里看海，有人七八个闹钟起不来。加油打工人！',
        '打工可能会少活十年，不打工你一天也活不下去。加油，打工人！',
        '打工不仅能致富，还能交友娶媳妇。打工人，打工魂，打工都是人上人。家人们加油!',
        '很喜欢打工人的一句话：打工赚不了几个钱，但是多打几份工可以让你没时间花钱。',
        '不打工没钱，打工也没钱，只不过打工一时饿不死而已，不打工很快就饿死。加油，打工人！',
        '打工累吗？累。但是我不能哭，因为骑电动车的时候擦眼泪不安全。',
        '我带上了头盔，就无法吻你，摘下了头盔被交警罚款50。加油，打工人！',
        '每天对着空气挥一拳，不为别的，就为干这个世界！加油，打工人！',
        '不是工作需要我，而是我需要工作，我打工，我快乐。加油，打工人！',
        '不拼爹，不拼娘，不拼工作，不拼钱，我们打工人只拼命。努力！打工人！',
        '天气变冷不像夏天的砖那么烫手了，就是总下雨滑不溜丢的。加油，打工人！',
        '敢上九天揽月，敢下五洋捉鳖，但却不敢迟到，因为迟到扣钱。加油，打工人 ！',
        '只要我够努力，老板很快就能过上他想要的生活！该起床奋斗了！加油，打工人！',
        '世上有两种最耀眼的光芒，一种是太阳，一种是打工人努力的模样。加油，打工人！',
        '你的微博好久没提及星球、银河、宇宙、梦想、文学了，怎么，是不是跟我一样去打工了？',
        '记住，打工才是王道，我们都要做打工人。人可以一天不吃饭，但不能一天不打工。打工让我们身心愉悦，节假日是掏空我们的身体。别睡了，打工仔，起床打工吧。',
        '如果你分手了就去西藏，可以疗伤。如果你单身就去丽江，可以艳遇。如果你喜欢我，就来深圳，可以跟我一起打工。中午好，打工人。',
        '老板对你说，加油小伙子，你好好努力，明年给你娶个漂亮的嫂子。加油，打工人！',
        '世上有两种最耀眼的光芒，一种是太阳，一种是打工人努力的模样。加油，打工人！',
        '打工人有二亩田，白天的一亩田是填饱肚子，晚上的一亩田是耕种自己的未来。打工人，雄起！',
        '你好像只会说一句话，我看了一下聊天记录，我们的聊天记录里，无论我说什么，你都只回一句：加油，打工人！',
        '锄禾曰当午,上班好辛苦。上完一上午,还要上下午。不上没钱花,心里更痛苦。好日子,辛苦就辛苦。加油，打工人！',
        '干不完的活，睡不够的觉，喂不胖的钱包，买不起的貂，半辈子就挣了两个亿，一个失忆，一个回忆。加油，打工人！',
        '你的同事，要不就是拆二代体验生活，要不就是关系户整天吊儿郎当。只有你，是真正在为生活奔波的打工仔。加油，打工人！',
        '三百六十行行行能起床，为啥只有你不行！五点后还没起的人，你根本没把自己生活当一回事，想必我们也不是一路人！加油 打工人！',
        '头等舱可以优先登机，银行VIP可以不用排队，演唱会最贵的票位置也最好，世界从不平等，你有多努力，就有多特殊。加油，打工人！',
        '加油，打工人。说“上班”，就感觉像是为生活所迫，不情不愿。说“打工”，就像是带着美好的憧憬，用努力和汗水去创造未来！加油！打工人！',
      ],
      holidayTips: [
        '老子今天不上班！',
        '别跟老子谈理想，老子的理想是不上班！',
        '打工是不可能打工的,这辈子都不可能打工的',
      ]
    }
  },
  methods: {
    getDateList() {
      this.dateList = []
      let offset = this.stime.getDay()===0?6:this.stime.getDay()-1
      for(let i=0;i<offset;i++){
        this.dateList.push({})
      }

      let tempDate = new Date(this.stime)
      let count = 0
      let workCount = 0
      while (tempDate<=this.etime) {
        count = count + 1
        let dateInfo = this.getDate(tempDate)
        if (dateInfo.holiday === false){
          workCount = workCount + 1
        }
        this.dateList.push(dateInfo)
        tempDate = new Date(tempDate.setDate(tempDate.getDate()+1))
      }
      this.dateList.count = count
      this.dateList.workCount = workCount
      this.setTips()
    
    },
    dateChange(){
      this.stime = this.daterange[0]
      this.etime = this.daterange[1]
      this.getDateList()
    },
    changeState(index,item){
      item.holiday = !item.holiday
      if(item.holiday){
        this.dateList.workCount = this.dateList.workCount - 1
        this.$message.success(item.date.split('-')[0]+'月'+item.date.split('-')[1]+'日 '+'切换为休息状态')
      } else {
        this.dateList.workCount = this.dateList.workCount + 1
        this.$message.success(item.date.split('-')[0]+'月'+item.date.split('-')[1]+'日 '+'切换为搬砖状态')
      }
      this.dateList[index] = item
    },
    setTips() {
      let date = new Date()
      let todayInfo = this.getDate(date)
      let weeks = ['日','一','二','三','四','五','六']
      this.today = todayInfo.date.split('-')[0]+'月'+todayInfo.date.split('-')[1]+'日 '+"星期"+weeks[date.getDay()]+' '+(todayInfo.holiday?"法定休息日":"法定打工日")
      if (todayInfo.holiday) {
        this.tips = this.holidayTips[Math.floor(Math.random()*this.holidayTips.length)]
      } else {
        this.tips = this.workTips[Math.floor(Math.random()*this.workTips.length)]
      }
    },
    getDate(date){
      let month = date.getMonth()+1<10?'0'+(date.getMonth()+1):date.getMonth()+1
      let day = date.getDate()<10?'0'+date.getDate():date.getDate()
      let date_str = month+'-'+day
      let isHoliday = date.getDay() === 6 || date.getDay() === 0
      isHoliday = this.holiday.holiday[date_str]?this.holiday.holiday[date_str].holiday:isHoliday
      return {
        date: date_str,
        holiday: isHoliday
      }
    },
    init(){
      let date = new Date()
      this.stime = new Date(`${date.getFullYear()}-${date.getMonth()+1}-1`)
      let nextMonth = new Date(`${date.getFullYear()}-${date.getMonth()+2}-1`)
      this.etime = new Date(nextMonth.setDate(nextMonth.getDate()-1))

      this.daterange = []
      this.daterange[0] = this.stime
      this.daterange[1] = this.etime

      this.axios.get("https://timor.tech/api/holiday/year/")
      .then(res=>{
        this.holiday = res.data
        this.getDateList()
      })
    }
  },
}
</script>

